import React, { Component } from "react";

export default class Inbox extends Component {
  constructor(props) {
    super(props);
    this.state = {
      btcBalance: "0.00"
    };
  }
  componentDidMount() {
    fetch("https://api.coindesk.com/v1/bpi/currentprice/INR.json")
      .then(res => res.json())
      .then(res => this.setState({ btcInINR: res.bpi.INR.rate }))
      .catch(err => console.log(err));
  }
  render() {
    return (
      <div>
        <div className="container">
          <div className="jumbotron">
            <h3>Inbox</h3>
            <div className="form-group">
              <label htmlFor="exampleInputWallet">your wallet address</label>
              <input readOnly ref="wallet" type="text" className="form-control " />
            </div>
            <hr />
            <form>
              <h3>Your Wallet</h3>
              <div className="form-group">
                <label>BTC</label>
                <input readOnly ref="BTbalance" type="number" className="form-control" />
                <small className="form-text text-muted">
                  It's worth in Doller
                </small>
                <input readOnly
                  ref="cashAmount"
                  type="number"
                  className="form-control "
                />
              </div>

              <button type="submit" className="btn btn-primary">
                Refresh
              </button>
            </form>
          </div>
        </div>
      </div>
    );
  }
}
