import React, { Component } from "react";
import { Link } from "react-router-dom";

const axios = require("axios");

export default class Signup extends Component {
  constructor(props) {
    super(props);
    this.state = {
      err: ""
    };
  }
  handleSignup = e => {
    e.preventDefault();
    const signupData = {
      name: this.refs.name.value,
      email: this.refs.email.value,
      password: this.refs.password.value
    };
    axios
      .request({
        method: "post",
        url: "https://loopback-react-btc-wallet.herokuapp.com/api/Users",
        data: signupData
      })
      .then(res => {
        this.props.history.push("/login");
      })
      .catch(err => {
        console.log(err);
        this.setState({
          err: err.response.data.error.message
        });
      });
  };
  render() {
    return (
      <div className="container">
        <Link className="btn btn-secondary mb-3 mt-3 btn-sm" to="/">
          <i className="fa fa-arrow-left" />
          <i className="fa fa-home ml-3" />
        </Link>
        <h3>Sign Up</h3>
        <form onSubmit={this.handleSignup.bind(this)}>
          <div className="form-group">
            <label htmlFor="exampleInputName">Enter your Name</label>
            <input
              ref="name"
              type="text"
              className="form-control"
              id="exampleInputName"
              placeholder="Enter your name"
            />
            <label htmlFor="exampleInputEmail1">Email address</label>
            <input
              ref="email"
              type="email"
              className="form-control"
              id="exampleInputEmail1"
              aria-describedby="emailHelp"
              placeholder="Enter email"
            />
            <small id="emailHelp" className="form-text text-muted">
              We'll never share your email with anyone else.
            </small>
          </div>
          <div className="form-group">
            <label htmlFor="exampleInputPassword1">Password</label>
            <input
              ref="password"
              type="password"
              className="form-control"
              id="exampleInputPassword1"
              placeholder="Password"
            />
          </div>

          <button type="submit" className="btn btn-primary">
            Submit
          </button>
          <div className="mt-3">
            <p className="text-warning">{this.state.err}</p>{" "}
          </div>
        </form>
      </div>
    );
  }
}
