import React, { Component } from "react";
import "./Navbar.css";
import { Redirect } from "react-router-dom";
const axios = require("axios");

export default class Navbar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userEmail: localStorage.getItem("userEmail"),
      logout: false,
      access_token: props.access_token,
      isNavOpen: false,
      btcInINR: <small className="text-warning">failed to fetch</small>,
      btcBalance: "0.00"
    };
  }
  componentDidMount() {
    fetch("https://api.coindesk.com/v1/bpi/currentprice/INR.json")
      .then(res => res.json())
      .then(res => this.setState({ btcInINR: res.bpi.INR.rate }))
      .catch(err => console.log(err));
  }
  handleNavbar = () => {
    this.setState({
      isNavOpen: !this.state.isNavOpen
    });
  };
  handleLogout = () => {
    axios
      .request({
        method: "post",
        url: `https://loopback-react-btc-wallet.herokuapp.com/api/Users/logout?access_token=${
          this.state.access_token
        }`
      })
      .then(res => {
        console.log("logged out");
        this.setState({
          logout: true
        });
      })
      .catch(err => {
        console.log(err);
      });
  };
  renderLogout = () => {
    if (this.state.logout) {
      return <Redirect to="/" />;
    }
  };
  render() {
    const left = this.state.isNavOpen ? "0" : "-100%";
    return (
      <div>
        <div>
          {this.renderLogout()}
          <div className="nav">
            <div className="bars">
              <i onClick={this.handleNavbar} className="fa fa-bars" />
            </div>
            <div className="rightNav">
              <div>
                <span>
                  Bitcoin balance <br /> <i className="mr-1 fab fa-bitcoin" />
                  {this.state.btcBalance}
                </span>
              </div>
              <div>
                <span>
                  Current Price <br /> <i className="mr-1 fas fa-rupee-sign" />
                  {this.state.btcInINR}
                </span>
              </div>
            </div>
          </div>

          <div className="sideBar" style={{ left: left }}>
            <ul>
              <li>
                Logged in as <br /> {this.state.userEmail}
              </li>
              <li>
                <button
                  onClick={this.handleLogout}
                  className="btn btn-block btn-danger"
                >
                  logout
                </button>
              </li>
            </ul>
          </div>
        </div>
      </div>
    );
  }
}
